.advantages {
  background-color: $bgc;
  padding: $p-sec;
  .row {
    align-items: flex-end;
    margin: -15px;
  }
  // .advantages__block
  &__block {
    display: flex;
    flex: 1 1 0%;
    flex-direction: column;
    justify-content: flex-end;
    min-height: 100px;
    margin: 15px;
    text-align: center;
    
    b {
      align-self: center;
      position: relative;
      z-index: 0;
      font-size: 40px;
      margin-bottom: 10px;
      &::before, &::after {
        content: "";
        position: absolute;
        transform: skew(-25deg);
      }
      &::before {
        z-index: -2;
        top: -28px;
        left: -20px;
        width: 46px;
        height: 80px;
        background-color: $main;
      }
      &::after {
        z-index: -1;
        top: 4px;
        left: -50px;
        width: 24px;
        height: 64px;
        background-color: $mainHover;
      }
    }
  }
}

  