  font-family: Roboto, sans-serif;
  font-size: 14px;
  color: #24242b; }

*, *:after, *:before {
  box-sizing: inherit; }

h1, h2, h3, h4, h5, h6, ul {
  margin: 0; }

ul {
  padding: 0;
  list-style: none; }

img {
  display: block; }

button {
  border: none;
  cursor: pointer;
  background-color: transparent;
  padding: 0;
  transition: 0.2s ease-in-out; }

/**** Медиа ****/
/****  Допольнительные теги****/
a {
  color: inherit;
  text-decoration: none;
  transition: 0.2s ease-in-out; }

input, button, textarea {
  font-size: inherit;
  color: inherit; }

input {
  padding: 0; }

a:focus, button:focus, input:focus, textarea:focus {
  box-shadow: 0 0 5px 3px #fcaf17, inset 0 0 0 1px #000;
  outline: none; }

.js-focus-visible :focus:not(.focus-visible) {
  transition: none;
  outline: none;
  box-shadow: none; }

small {
  font-size: 12px; }

