.input-search {
  position: relative; }
  .input-search:focus-within .input-search__placeholder, .input-search input:not(:invalid) ~ .input-search__placeholder {
    top: -0.6em;
    left: 0;
    transition: color 0.2s ease-in-out, left 0.2s ease-in-out, top 0.2s ease-in-out 0.2s, background-color 0.2s ease-in-out 0.2s;
    background-color: #e8e8ea; }
    .input-search:focus-within .input-search__placeholder::before, .input-search input:not(:invalid) ~ .input-search__placeholder::before {
      width: 100%;
      height: calc(40% * 1.15 + 2px);
      /* умножаем на высоту строки + padding "placeholder" */
      transition: border-color 0.2s ease-in-out, border-width 0.2s ease-in-out 0.2s, width 0.2s ease-in-out calc(0.2s + .1s), height 0.2s ease-in-out calc(0.2s + .2s);
      border-width: 1px; }
  .input-search:focus-within .input-search__icon, .input-search input:not(:invalid) ~ .input-search__icon {
    font-size: 0;
    transition: 0.2s ease-in-out; }
  .input-search:focus-within .input-search__placeholder {
    color: #fff; }
    .input-search:focus-within .input-search__placeholder::before {
      border-color: transparent #fff #fff transparent; }
  .input-search input {
    width: 100%;
    border: 1px solid #bfc1bf;
    background-color: #fff;
    padding: 1em;
    transition: 0.2s ease-in-out; }
    .input-search input::placeholder {
      opacity: 0; }
    .input-search input:hover, .input-search input:focus {
      border: 1px solid #fff; }
  .input-search__placeholder {
    position: absolute;
    z-index: 1;
    top: calc(1em - 1px);
    /*  -бордер инпута */
    left: calc(1em - 5px + 1px);
    /*  -паддинг и -бордер инпута */
    padding: 2px 5px;
    pointer-events: none;
    user-select: none;
    background-color: inherit;
    transition: color 0.2s ease-in-out, left 0.2s ease-in-out calc(0.2s + .3s), top 0.2s ease-in-out 0.3s, background-color 0.2s ease-in-out 0.3s;
    color: #000; }
    .input-search__placeholder::before {
      content: "";
      position: absolute;
      z-index: -1;
      right: 0;
      bottom: 0;
      left: 0;
      width: 0;
      height: 1px;
      margin: 0 auto;
      border-color: transparent #bfc1bf #bfc1bf transparent;
      border-style: solid;
      border-width: 0;
      transition: border-color 0.2s ease-in-out, border-width 0.2s ease-in-out calc(0.2s + .2s), width 0.2s ease-in-out calc(0.2s + .1s), height 0.2s ease-in-out; }
  .input-search__icon {
    position: absolute;
    top: 50%;
    right: calc(1rem - 1px);
    /*  -паддинг и -бордер инпута */
    transform: translateY(-50%);
    transition: 0.2s ease-in-out calc(0.2s + .3s);
    font-size: 15px;
    pointer-events: none;
    user-select: none; }
    .input-search__icon:before {
      /* Эта иконка должны быть тоньше, но она платная */ }

.scroll-up {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 10;
  right: 15px;
  bottom: -60px;
  width: 45px;
  height: 45px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  background-color: #fff;
  color: #24242b; }
  .scroll-up:hover {
    color: #fcaf17; }
  .scroll-up.show {
    bottom: 15px; }
    @media (max-width: 767.98px) {
      .scroll-up.show {
        bottom: 15px; } }
  .scroll-up.absolute {
    position: absolute;
    top: -60px;
    bottom: unset; }
    @media (max-width: 767.98px) {
      .scroll-up.absolute {
        top: 15px; } }

