.form {
  // .form__inputs
  padding: 25px 0;
  &__inputs {
    display: flex;
    margin: 0 -5px 10px;

    input {
      height: 40px;
      border-radius: 2px;
      border: none;
      color: $text;
      text-align: center;
      padding: 0 10px;
      transition: $trans;
      &::placeholder {
        transition: $trans;
        opacity: 1;
        color: $text;
      }
      &:hover {
        color: $main;
        transition: $trans;
        &::placeholder {
          color: $main;
        }
      }
    }

    input, .btn {
      width: 230px;
      margin: 0 5px;
    }
  }
  small {
    font-size: 10px;
    a {
      color: $main;
      &:hover {
        text-decoration: underline;
        color: $mainHover;
      }
    }
  }
}