.heading {
  background-image: url("/img/heading/bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 570px;
  color: #fff;
  padding: 180px 0 200px;
  background-color: $bgcBlack;
  .title-h1 {
    color: #fff;
    margin-bottom: $mb-t;
  }
  // .heading__text
  &__text {
    max-width: 600px;
  }
  // .heading__form
  &__form {
    max-width: 60%;
  }
}

  