.scroll-up {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 10;
  right: 15px;
  bottom: -60px;
  width: 45px;
  height: 45px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25);
  border-radius: 2px;
  background-color: #fff;
  color: $text;

  &:hover {
    color: $main;
    // background-color: $main;
  }

  &.show {
    @include max-767 {
      bottom: 15px;
    }
    bottom: 15px;
  }

  &.absolute {
    @include max-767 {
      top: 15px;
    }
    position: absolute;
    top: -60px;
    bottom: unset;
  }
}