.trust {
  padding: $p-sec;
  .container {
    display: flex;
  }
  // .trust__left-column
  &__left-column {
    background-color: $main;
    flex: 1 1 50%;
    padding: 50px;
    margin-right: 30px;

    li {
      display: flex;
      font-size: 18px;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
      &:before {
        content: url("/img/icons/check.svg");
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: center;
        width: 25px;
        height: 25px;
        background-color: $bgcBlack;
        border-radius: 50%;
        margin: 2px 30px 0 0;
      }
    }
  }
  // .trust__right-column
  &__right-column {
    display: flex;
    flex: 1 1 50%;
    flex-direction: column;
  }

  //.trust__right-block
  &__right-block {
    display: flex;
    flex-direction: column;
    background-color: $bgc;
    padding: 15px 40px;
    b {
      margin-bottom: 15px;
    }
  }

  // .trust__img
  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    margin-bottom: 20px;
  }
  // .trust__quality
  &__quality {
    margin-bottom: 30px;
    p {
      max-width: 420px;
    }
    .trust__img {
      background-color: $bgcBlack;
    }
  }
  // .trust__sell
  &__sell {
    position: relative;
    z-index: 0;
    color: #fff;
    background-image: url("/img/trust/bg.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    p {
      max-width: 320px;
    }

    &:before {
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: linear-gradient(90deg, $bgcBlack 0%, rgba($bgcBlack, 0) 63.33%);
    }

    .trust__img {
      background-color: $main;
    }
  }
}


  