.footer {
  position: relative;
  background-color: $bgcBlack;
  background-image: url("/img/footer/bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  padding: 60px 0 20px;
  color: #fff;
  .row {
    justify-content: space-between;
    margin-bottom: 40px;
  }
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
      padding: 5px 0;
      &:hover {
        color: $main;
        text-decoration: underline;
      }
    }
  }
}