/**** Базовые ****/
html {
  box-sizing: border-box;
  // scroll-behavior: smooth; Не работает в IE =(
  font-family: Roboto, sans-serif; //Базовые шрифты
  font-size: 14px; //Базовый размер шрифта
  color: $text;
}

*, *:after, *:before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, ul {
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

img {
  display: block;
}

button {
  border: none;
  cursor: pointer;
  background-color: transparent;
  padding: 0;
  transition: $trans; // доп. тег
}

/**** Медиа ****/
@mixin max-539 {
  @media (max-width: 539.98px) {
    @content;
  }
}

@mixin min-540 {
  @media (min-width: 540px) {
    @content;
  }
}

@mixin max-767 {
  @media (max-width: 767.98px) {
    @content;
  }
}

@mixin min-768 {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin max-1023 {
  @media (max-width: 1023.98px) {
    @content;
  }
}

@mixin min-1024 {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin max-1199 {
  @media (max-width: 1199.98px) {
    @content;
  }
}

@mixin min-1200 {
  @media (min-width: 1200px) {
    @content;
  }
}

@mixin max-1599 {
  @media (max-width: 1599.98px) {
    @content;
  }
}
@mixin min-1600 {
  @media (min-width: 1600px) {
    @content;
  }
}


@mixin hover {
  &:not([disabled]):hover {
    @content;
  }
}
/****  Допольнительные теги****/

a {
  color: inherit;
  text-decoration: none;
  transition: $trans;
}

input, button, textarea {
  // font-family: inherit;
  font-size: inherit;
  color: inherit;
}

input {
  padding: 0;
}


a:focus, button:focus, input:focus, textarea:focus {
  box-shadow: 0 0 5px 3px $main, inset 0 0 0 1px #000;
  outline: none;
}

//полифил на focus-visible, нет фокуса
.js-focus-visible :focus:not(.focus-visible) {
  transition: none;
  outline: none;
  box-shadow: none;
}

small {
  font-size: 12px;
}

