 */

.container {
  @include container();
  /*   @include sm(max-width, 540px);
  @include md(max-width, 700px);
  @include lg(max-width, 990px); */
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin: -1px;
  clip: rect(0 0 0 0);
  border: 0;
  padding: 0;
}

.title-h1 {
  font-size: 40px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  line-height: 1.25;
}

.title-h2 {
  font-size: 25px;
  font-weight: 700;
}

.subtitle {
  font-size: 18px;
}

.btn {
  height: 40px;
  padding: 10px;
  background-color: $main;
  border-radius: 2px;
  font-weight: 700;
  
  &:hover {
    background-color: $mainHover;
  }
}

.btn-close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 20px;
  height: 20px;
  transition: $trans;
  box-shadow: inset 0 0 0 0 $error;

  &::before, &::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 1px;
    background-color: #000;
    transition: $trans;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:hover, &:focus {
    // background-color: $error;
    box-shadow: inset 0 0 0 20px $error;
    &::before, &::after {
      height: 2px;
      transform: rotate(0);
      background-color: #fff;
    }
  }
}

.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1000;
  top: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  transition: background-color $trans;
  background-color: rgba(0, 0, 0, 0);

  // .modal__content
  &__content {
    position: relative;
    max-width: 500px;
    padding: 25px 15px 30px;
    transform: scale(0);
    transition: $trans;
    background-color: #fff;
  }
}


