.contacts {
  position: relative;
  max-width: 1920px;
  min-height: 400px;
  margin: 0 auto;
  padding: 15px 0;
  // .contacts__block
  &__block {
    position: relative;
    z-index: 1;
    max-width: 400px;
    border: 2px solid $text;
    padding: 30px;
    background-color: #fff;
    margin-left: 60px;
    .title-h2 {
      margin-bottom: $mb-t;
    }
    li {
      display: flex;
      flex-direction: column;
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
    b {
      font-weight: 400;
    }
    span, a {
      color: rgba($text, 0.6);
    }

    a:hover {
      color: $main;
      text-decoration: underline;
    }
  }
  //.contacts__map
  &__map {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    max-width: 100%;
    overflow: hidden;
    [class*="ymaps-2"][class*="-ground-pane"] {
      filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale");
    }
    [class*="ymaps-2"][class*="-copyrights-pane"], [class*="ymaps-2"][class*="-controls-pane"] {
      display: none;
    }
    // overflow: hidden;
  }
}
