.clients {
  padding: $p-sec;
  background-color: $bgc;
  .title-h1 {
    text-align: center;
    margin-bottom: $mb-t;
  }
  .row {
    justify-content: space-between;
    margin: -10px;
  }
}


.client {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 200px;
  max-height: 200px;
  margin: 10px;
  border: 1px solid $main;
  padding: 30px;
  background-color: #fff;
  transition: $trans;
  border-radius: 2px;
  img {
    max-width: 100%;
    filter: grayscale(1);
    transition: $trans;
  }

  &:hover {
    border: 1px solid $mainHover;
    img {
      filter: grayscale(0);
    }
  }
}
