.logo {
  display: block;
  position: relative;
  transition: $trans;
  color: #fff;
  &:hover {
    color: $main;
    .logo__ring {
      color: $mainHover;
    }
  }
  // .logo__ring
  &__ring {
    transition: $trans;
    color: $main;
  }
  small {
    position: absolute;
    bottom: 1em;
    left: 0;
    font-size: 8px;
    font-weight: 700;
    letter-spacing: 0.2em;
    text-transform: uppercase;
  }
}