.produtcs {
  padding: $p-sec;
  .title-h1 {
    margin-bottom: $mb-t;
    text-align: center;
  }
  &__row {
    display: flex;
    justify-content: space-between;
    margin: 0 -15px;
  }
}


.product {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 15px;
  b {
    display: block;
    flex-grow: 1;
    text-align: center;
    margin-bottom: 40px;
  }
  // .product__img
  &__img {
    margin-bottom: 40px;
    img {
      max-width: 100%;
    }
  }

  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 -15px;
  }

  span:not([aria-hidden]) {
    flex: 0 0 auto;
    margin: 0 15px;
  }
  span[aria-hidden] {
    flex: 1 1 100%;
    height: 1px;
    border-top: 1px dashed #000;
  }
}
