.contacts-block {
  display: flex;
  color: #fff;
  align-items: center;

  .btn {
    width: 175px;
  }

  // .contacts-block__links
  &__links {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-right: 30px;
    
    a {
      display: flex;
      align-items: center;
      align-self: flex-start;
      padding: 5px;
      
      &:hover {
        color: $main;
        text-decoration: underline;
      }
    }

    svg {
      margin-right: 0.5em;
    }
  }

  &__tel svg {
    width: 1em;
    height: 1em;
  }
  &__mail svg {
    width: 1.4em;
    height: 0.9em;
  }
}
